import React from 'react'
import styled from 'styled-components'
import { Heading, Text, BaseLayout } from 'uikit'
import Page from 'components/Layout/Page'
import WalletCard from './components/WalletCard'
import Tokenomics from './components/Tokenomics' 
import PriceChart from './components/PriceChart' 
import ZapperData from './components/ZapperData' 

 
const Hero = styled.div`
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: auto;
  margin-bottom: 32px;
  padding-top: 32px;
  text-align: center;
`

const Cards = styled(BaseLayout)`
  align-items: stretch;
  justify-content: stretch;
  margin-bottom: 24px;
  grid-gap: 24px;

  & > div {
    grid-column: span 6;
    width: 100%;
  }

  ${({ theme }) => theme.mediaQueries.sm} {
    & > div {
      grid-column: span 8;
    }
  }

  ${({ theme }) => theme.mediaQueries.lg} {
    margin-bottom: 32px;
    grid-gap: 32px;

    & > div {
      grid-column: span 6;
    }
  }
`

const Home: React.FC = () => {

  return (
    <>
      <Page>
        <Hero>
          <Heading as="h1" scale="xl" mb="24px" color="text">
            My Dashboard
          </Heading>
          <Text color="textSubtle">As market veterans we find best and safest return investment opportunities.</Text>
        </Hero>
        <div>
          <Cards>
            <WalletCard />
            <Tokenomics />
          </Cards>
          <Cards>
            <PriceChart />
            <ZapperData />
          </Cards>
        </div>
      </Page>
    </>
  )
}

export default Home
