import React, { useState, useEffect} from 'react'
import styled from 'styled-components'
import axios from 'axios'
import { Card, CardBody, Text, Heading, CopyToClipboard } from 'uikit'
import contracts from 'config/contracts'

const StyledCard = styled(Card)`
  min-height: 376px;
`

const Block = styled.div`
  margin-bottom: 16px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
`

const Label = styled.div`
  color: ${({ theme }) => theme.colors.textSubtle};
`
const StyledCardBody = styled(CardBody)`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

const Tokenomics = () => {
  const [holders, setHolders] = useState(0)

  useEffect(() => {
    axios.get(`https://api.ethplorer.io/getTokenInfo/${contracts.fsv}?apiKey=${process.env.REACT_APP_ETHPLORER_KEY}`).then(res=>{
      if(res.data && res.data.countOps) {
        setHolders(res.data.countOps)
      }
    }).catch(err=>{
      console.error('axios fetch error:', err.data)
    })
  }, [])
  return (
    <StyledCard>
      <StyledCardBody>
        <Heading scale='lg' mb='24px'>DATA</Heading>
        <Block>
          <Label>Token Ticker</Label>
          <Text fontSize='18px'>FSV</Text>
        </Block>
        <Block>
          <Label>Token Type</Label>
          <Text fontSize='18px'>ERC-20 Reflection Token</Text>
        </Block>
        <Block>
          <Label>Buy/Sell Tax</Label>
          <Text fontSize='18px'>10%</Text>
        </Block>
        <Block>
          <Label>Total Supply</Label>
          <Text fontSize='18px'>1,000,000,000</Text>
        </Block>
        <Block>
          <Label>Holders</Label>
          <Text fontSize='18px'>{holders}</Text>
        </Block>
        <Block>
          <Label>Contract</Label>
          <div style={{'display':'flex'}}>
            <Text fontSize='18px'>0x643239d.....94bc64E5f</Text>
            <CopyToClipboard toCopy='0x643239d5d7F05eD6D268c371aBF5eF694bc64E5f' />
          </div>
        </Block>
      </StyledCardBody>
    </StyledCard>
  )
}

export default Tokenomics