import React from 'react'
import styled from 'styled-components'
import { Card, CardBody, Text, Heading, CopyToClipboard } from 'uikit'
import {useEthBalance} from 'utils/getBalance'
import CardValue from 'components/CardValue'


const StyledCard = styled(Card)`
  min-height: 376px;
`

const Block = styled.div`
  margin-bottom: 10px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: space-between;
`

const Label = styled.div`
  color: ${({ theme }) => theme.colors.textSubtle};
`

const StyledCardBody = styled(CardBody)`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`
const BlockWrapper = styled.div`
  margin-top: 10px;
  padding-left: 20px;
`
const ZapperData = () => {

  const treasureWallet = '0xAF59E55e72C346E070Ce1ffd07398e7Cee882fb5'
  const marketingWallet = '0x01a6a0334f310e1E89bfd7daf609f2F17B3E1Eb3'
  const devWallet = '0xeC14e5C2d029Cb60e7629d59870eD00Aa9863Ad1'

  const { balance: treasureEth }  = useEthBalance(treasureWallet)
  const { balance: marketingEth }  = useEthBalance(marketingWallet)
  const { balance: devEth }  = useEthBalance(devWallet)
  return (
    <StyledCard>
      <StyledCardBody>
        <Heading scale='lg' mb='24px'>Zapper Data</Heading>
        <div>
          <Text fontSize='18px'>Treasure Wallet</Text>
          <BlockWrapper>
            <Block>
              <Label>Address</Label>
              <div style={{ 'display': 'flex' }}>
                <Text fontSize='18px'>{treasureWallet.slice(0, 7)}.....{treasureWallet.slice(-7)}</Text>
                <CopyToClipboard toCopy={treasureWallet} />
              </div>
            </Block>
            <Block>
              <Label>Eth in Wallet</Label>
              <CardValue value={treasureEth} decimals={5} fontSize="18px" lineHeight="36px" />
            </Block>
          </BlockWrapper>
        </div>
        <div>
          <Text fontSize='18px'>Marketing Wallet</Text>
          <BlockWrapper>
            <Block>
              <Label>Address</Label>
              <div style={{ 'display': 'flex' }}>
                <Text fontSize='18px'>{marketingWallet.slice(0, 7)}.....{marketingWallet.slice(-7)}</Text>
                <CopyToClipboard toCopy={marketingWallet} />
              </div>
            </Block>
            <Block>
              <Label>Eth in Wallet</Label>
              <CardValue value={marketingEth} decimals={5} fontSize="18px" lineHeight="36px" />
            </Block>
          </BlockWrapper>
        </div>
        <div>
          <Text fontSize='18px'>Dev Wallet</Text>
          <BlockWrapper>
            <Block>
              <Label>Address</Label>
              <div style={{ 'display': 'flex' }}>
                <Text fontSize='18px'>{devWallet.slice(0, 7)}.....{devWallet.slice(-7)}</Text>
                <CopyToClipboard toCopy={devWallet} />
              </div>
            </Block>
            <Block>
              <Label>Eth in Wallet</Label>
              <CardValue value={devEth} decimals={5} fontSize="18px" lineHeight="36px" />
            </Block>
          </BlockWrapper>
        </div>
      </StyledCardBody>
    </StyledCard>
  )
}

export default ZapperData