import React from 'react'
import styled from 'styled-components'
import { useSelector } from 'react-redux'
import { useWeb3React } from '@web3-react/core'
import { Card, CardBody, Text, Heading } from 'uikit'
import ConnectWalletButton from 'components/ConnectWalletButton'
import CardValue from 'components/CardValue'
import { useFsvBalance, useEthBalance } from 'state/user/hooks'
import CardBusdValue from 'components/CardBusdValue'
import { State } from 'state/types'

const StyledCard = styled(Card)`
  min-height: 376px;
`

const Block = styled.div`
  margin-bottom: 16px;
`

const Label = styled.div`
  color: ${({ theme }) => theme.colors.textSubtle};
`
const Actions = styled.div`
  margin-top: 24px;
`
const StyledCardBody = styled(CardBody)`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`

const WalletCard = () => {
  const { account } = useWeb3React()
  const { balance: fsvBalance } = useFsvBalance(account)
  const { balance: ethBalance } = useEthBalance(account)
  const { ethPrice } = useSelector((state: State) => ({
    ethPrice: state.info.ethPrice
  }))
  const { fsvPrice } = useSelector((state: State) => ({
    fsvPrice: state.info.fsvPrice
  }))

  console.info('pricessssssssss', ethPrice, fsvPrice)

  return (
    <StyledCard>
      <StyledCardBody>
        <Heading scale='lg' mb='24px'>Your Wallet Data</Heading>
        <Block>
          <Label>ETH in Wallet</Label>
          {
            !account ?
              <Text color="textDisabled" style={{ lineHeight: '54px' }}>Locked</Text>
              :
              <>
                <CardValue value={ethBalance} decimals={5} fontSize="24px" lineHeight="36px" />
                <CardBusdValue value={ethBalance*ethPrice} />
              </>
          }
        </Block>
        <Block>
          <Label>FSV in Wallet</Label>
          {
            !account ?
              <Text color="textDisabled" style={{ lineHeight: '54px' }}>Locked</Text>
              :
              <>
                <CardValue value={fsvBalance} decimals={5} fontSize="24px" lineHeight="36px" />
                <CardBusdValue value={fsvBalance*fsvPrice} />
              </>
          }
        </Block>
        <Actions>
          {
            !account && <ConnectWalletButton width='100%' />
          }
        </Actions>
      </StyledCardBody>
    </StyledCard>
  )
}

export default WalletCard