import { useMemo } from "react";
import { ethers } from "ethers";
import addresses from 'config/contracts'
import useActiveWeb3React from "hooks/useActiveWeb3React";
import fsvAbi from 'config/abis/fsvToken.json'
import { simpleRpcProvider } from "./provider";


export const getFsvAddress = () => {
  return addresses.fsv
}

const getContract = (abi: any, address: string, signer?: ethers.Signer | ethers.providers.Provider) => {
  const signerOrProvider = signer ?? simpleRpcProvider
  return new ethers.Contract(address, abi, signerOrProvider)
}

export const getFsvContract = (signer?: ethers.Signer | ethers.providers.Provider) => {
  return getContract(fsvAbi, getFsvAddress(), signer)
}

export const useLeosContract = () => {
  const { library } = useActiveWeb3React()
  return useMemo(() => getFsvContract(library.getSigner()), [library])
}

