import { useEffect } from "react";
import { useSelector } from "react-redux";
import { useAppDispatch } from "state";
import { State } from "state/types";
import useRefresh from 'hooks/useRefresh'


import { fetchFSVBalance, fetchETHBalance } from ".";

export const useFsvBalance = (account): { balance: number } => {
  const { fastRefresh } = useRefresh()
  const dispatch = useAppDispatch()
  useEffect(() => {
    if (account) {
      dispatch(fetchFSVBalance(account))
    }
  }, [account, dispatch, fastRefresh])
  const { balance } = useSelector((state: State) => ({
    balance: state.user.fsvBalance
  }))
  return { balance }
}

export const useEthBalance = (account): { balance: number } => {
  const { fastRefresh } = useRefresh()
  const dispatch = useAppDispatch()
  useEffect(() => {
    if (account) {
      dispatch(fetchETHBalance(account))
    }
  }, [account, dispatch, fastRefresh])
  const { balance } = useSelector((state: State) => ({
    balance: state.user.ethBalance
  }))
  return { balance }
}
